.containerOrderFeedInformation {
    display: flex;
    flex-direction: column;
    width: 100%;
    max-width: 600px;
    row-gap: 60px;
}
.completeTextOrder {
    color: #00CCCC;
}
.completeTextOrderAllTime {
    text-shadow: 0 4px 32px rgba(51, 51, 255, 0.50), 0 0 8px rgba(51, 51, 255, 0.25), 0 0 16px rgba(51, 51, 255, 0.25);
}
.containerCompleteWork {
    display: flex;
    column-gap: 36px;
}
.subContainerCompleteWork {
    width: 100%;
}
.subContainerNumberOrder {
    display: flex;
    row-gap: 8px;
    flex-wrap: wrap;
    height: 150px;
    flex-direction: column;
}